const state = {
  name: "下忍鸣人",
};
const mutations = {
  EditName: (state, name) => {
    state.name = name;
  },
};
const actions = {
  EditNameAsyc: ({ commit }, name) => {
    setTimeout(() => {
      commit("EditName", name);
    }, 2000);
  },
};

export default {
  namespaced: true,
  state,
  mutations,
  actions,
};
